.token {
  font-weight: 400;
}

.token.cdata,
.token.comment,
.token.prolog {
  color: #b362ff;
}

.token.atrule,
.token.delimiter,
.token.important,
.token.keyword,
.token.selector {
  color: #ff9d00;
}

.token.attr-name,
.token.operator {
