}

.token.boolean {
  color: #ff628c;
}

.token.builtin,
.token.doctype,
.token.tag,
.token.tag .punctuation {
  color: #ff9d00;
}

.token.entity,
.token.symbol {
  color: #6897bb;
}

.token.number {
  color: #ff628c;
}

.token.constant,
.token.property,
.token.variable {
  color: #ff628c;
}

.token.char,
.token.string {
